import React, { SFC, useEffect, useState } from 'react'
import { stickeryDB } from '../../../srv/db'
const GridGallery = require('react-grid-gallery')
export interface Image {
  src: string
  thumbnail: string
  thumbnailWidth: number
  thumbnailHeight: number
}
export interface ImportedImageGalleryHOC {}
export const ImportedImageGalleryHOC: SFC<ImportedImageGalleryHOC> = () => {
  const [images, setImages] = useState<Image[]>([])
  useEffect(() => {
    stickeryDB.images.toArray().then((imgs) => {
      const _images: Image[] = imgs.map((img) => {
        const src = `/_/images/${img.id!}/${img.name}`
        return { src, thumbnail: src, thumbnailWidth: 0, thumbnailHeight: 0 }
      })

      setImages(_images)
    })
  }, [])
  return <GridGallery images={images} />
}
